.optionsList {
  padding: 0;
  display: flex;
  justify-content: center;
  margin: 0;
  list-style: none;
  font-style: normal;
}

.buttonItem {
  padding: 0;
  margin: 0;
}

.buttonItem:not(:last-child) {
  margin-right: 5px;

  @media screen and (min-width: 480px) {
    margin-right: 20px;
  }
}
