.statsList {
  margin: 0;
  padding: 0 10px;
  list-style: none;
  color: #fff;
}

.statsItem {
  font-size: 16px;
  font-weight: 400;
  line-height: 1.5;
  letter-spacing: 0.02em;

  @media screen and (min-width: 480px) {
    font-size: 18px;
  }

  @media screen and (min-width: 768px) {
    font-size: 22px;
  }

  &:not(:last-child) {
    margin-bottom: 5px;
  }

  &:last-child,
  &:nth-last-child(2) {
    color: #30d5c8;
    font-weight: 600;
  }
}
