.message {
  margin: 0;
  padding: 8px;
  text-align: center;
  color: red;
  font-size: 16px;

  @media screen and (min-width: 480px) {
    font-size: 20px;
  }

  @media screen and (min-width: 768px) {
    font-size: 24px;
  }

  @media screen and (min-width: 1200px) {
    font-size: 30px;
  }
}
